import React, { Component } from 'react'

class TotalCovid extends Component {
    constructor(){
        super();
        this.state = {
            total : [],
        }
    }

    componentDidMount(){
        fetch("https://corona.lmao.ninja/all")
        .then(res => res.json())
        .then(json => {
          this.setState({
            total: json
          });
        });
    }
    render() {
        var { total } = this.state;
        return (
            <div>
                <div className="col-xl-12">
                <p>সারা বিশ্বের কোরনা Update</p>
                <div className="whole-world">
                <ul>
                    <li>আক্রান্ত : <span style={{background:"#8E2DE2",color: "#fff"}}>{total.cases}</span> </li>
                    <li>মৃত : <span style={{background:"#ED213A",color: "#fff"}}>{total.deaths}</span></li>
                    <li>সুস্থ হয়েছে : <span style={{background:"#667db6",color: "#fff"}}>{total.recovered}</span></li>
              
                </ul>
                </div>  
                </div>
            </div>
        )
    }
}

export default TotalCovid;